.preloader {
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: #262566;
    top: 0;
    left: 0;
    z-index: 99999
}

.preloader .lds-ripple {
    position: relative;
    width: 80px;
    height: 80px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
}

.preloader .lds-ripple div {
    position: absolute;
    border: 4px solid #fff;
    opacity: 1;
    border-radius: 50%;
    -webkit-animation: lds-ripple 1s cubic-bezier(0, .2, .8, 1) infinite;
    animation: lds-ripple 1s cubic-bezier(0, .2, .8, 1) infinite
}

.preloader .lds-ripple div:nth-child(2) {
    -webkit-animation-delay: -.5s;
    animation-delay: -.5s
}

@-webkit-keyframes lds-ripple {
    0% {
        top: 28px;
        left: 28px;
        width: 0;
        height: 0;
        opacity: 1
    }

    to {
        top: -1px;
        left: -1px;
        width: 58px;
        height: 58px;
        opacity: 0
    }
}

@keyframes lds-ripple {
    0% {
        top: 28px;
        left: 28px;
        width: 0;
        height: 0;
        opacity: 1
    }

    to {
        top: -1px;
        left: -1px;
        width: 58px;
        height: 58px;
        opacity: 0
    }
}

.go-top {
    position: fixed;
    cursor: pointer;
    top: 98%;
    left: -10%;
    background-color: #31abfc;
    z-index: 4;
    width: 40px;
    text-align: center;
    height: 42px;
    line-height: 42px;
    opacity: 0;
    visibility: hidden;
    transition: .9s
}

.go-top i {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
    color: #fff;
    transition: .5s;
    font-size: 20px
}

.go-top i:last-child {
    opacity: 0;
    visibility: hidden;
    top: 60%
}

.go-top:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: #31abfc;
    opacity: 0;
    visibility: hidden;
    transition: .5s
}

.go-top:hover {
    color: #fff;
    background-color: #262566
}

.go-top:hover:before {
    opacity: 1;
    visibility: visible
}

.go-top:hover i:first-child {
    opacity: 0;
    top: 0;
    visibility: hidden
}

.go-top:hover i:last-child {
    opacity: 1;
    visibility: visible;
    top: 50%
}

.go-top:focus {
    color: #fff
}

.go-top:focus:before {
    opacity: 1;
    visibility: visible
}

.go-top:focus i:first-child {
    opacity: 0;
    top: 0;
    visibility: hidden
}

.go-top:focus i:last-child {
    opacity: 1;
    visibility: visible;
    top: 50%
}

.go-top.active {
    transform: translateY(-98%);
    opacity: 1;
    visibility: visible;
    left: 95%
}

